import React from 'react'
import Affair from './Affair'
import s from './Affairs.module.css'
import {AffairType, FilterType} from './HW2'
import SuperButton from "../h4/common/c2-SuperButton/SuperButton";

type AffairsPropsType = { // need to fix any
    data: Array<AffairType>
    setFilter: (filter: FilterType) => void
    deleteAffairCallback: (_id: number) => void
}

function Affairs(props: AffairsPropsType) {
    const mappedAffairs = props.data.map((a: AffairType) => (
        <Affair // should work
            key={a._id} // кеи ОБЯЗАТЕЛЬНЫ в 99% - так что лучше их писать всегда при создании компонент в мапе
            affair={a}
            deleteAffairCallback={props.deleteAffairCallback}
        />
    ))

    const setAll = () => {
        props.setFilter('all')
    } // need to fix
    const setHigh = () => {
        props.setFilter('high')
    }
    const setMiddle = () => {
        props.setFilter('middle')
    }
    const setLow = () => {
        props.setFilter('low')
    }

    return (
        <div className={s.someClass}>
            {mappedAffairs}
            <div className={s.buttonBlock}>
                <div className={s.buttonAll}>
                    <SuperButton onClick={setAll} className={s.buttonFilter}>All</SuperButton>
                </div>
                <div className={s.buttonHigh}>
                    <SuperButton onClick={setHigh} className={s.buttonFilter}>High</SuperButton>
                </div>
                <div className={s.buttonMiddle}>
                    <SuperButton onClick={setMiddle} className={s.buttonFilter}>Middle</SuperButton>
                </div>
                <div className={s.buttonLow}>
                    <SuperButton onClick={setLow} className={s.buttonFilter}>Low</SuperButton>
                </div>
            </div>

        </div>
    )
}

export default Affairs
